.event-info-box {
  background-color: #B9D9EB;
  color: #114448;
  padding: 15px;
  text-align: center;
  border: 1px solid #114448;
  font-weight: bold;
  font-size: 1.05rem;
  flex: 1;
  text-decoration: none;
}

.event-info-box.link:hover {
  background-color: #d1e6f0;
  cursor: pointer;
}
